/****************************************************************************************/
/* nav header */
/****************************************************************************************/
.header-navbar .navbar {
  background-color: transparent; }

/* hide search */
.nav-item.search {
  display: none !important; }

.header-navbar .navbar-toggler,
.header-navbar .nav-search .btn-nav-search {
  color: #ffffff; }

.header-navbar .navbar-toggler:hover,
.header-navbar .nav-search .btn-nav-search:hover,
.header-navbar .navbar-toggler:focus,
.header-navbar .nav-search .btn-nav-search:focus {
  color: #d2d2d2; }

.header-navbar .navbar-small,
.blog .header-navbar,
.archive .header-navbar,
.single-post .header-navbar,
.error404 .header-navbar,
.search .header-navbar {
  background-color: #4CAAB1; }

.header-navbar .justify-content-center {
  -webkit-box-pack: end flex-end !important;
  -ms-flex-pack: end !important;
      justify-content: flex-end !important; }

.dropdown-toggle::after {
  border-right: none;
  border-left: none; }

.header-navbar .navbar .nav .dropdown-toggle::after {
  content: ' \f0e7';
  font-family: FontAwesome;
  margin: 0px;
  padding: 1px 0px 0px 2px;
  border: none;
  font-size: 10px; }

.header-navbar .navbar .nav .nav-item,
.header-navbar .navbar .nav .nav-item .dropdown-item,
.header-navbar .navbar .nav .nav-item .nav-link,
.header-navbar .navbar .nav .search,
.header-navbar .navbar .nav a::after {
  line-height: 1;
  text-shadow: 0.5px 0.866px 4px rgba(0, 0, 0, 0.92); }

.header-navbar .navbar .nav .nav-item {
  margin: 0px 14px; }

.header-navbar .navbar .nav .nav-item .dropdown-menu .nav-item {
  margin: 0px 6px; }

.header-navbar .navbar .nav > .nav-item > .nav-link:before {
  content: "";
  position: absolute;
  z-index: -1;
  left: 0;
  right: 100%;
  bottom: 0;
  background: #ffffff;
  height: 4px;
  -webkit-transition-property: right;
  transition-property: right;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out; }

.header-navbar .navbar .nav > .nav-item:hover > .nav-link:before,
.header-navbar .navbar .nav > .nav-item.nav-highlight > .nav-link:before {
  right: 0; }

.header-navbar .navbar .nav .search .nav-search a {
  font-size: 14px; }

.header-navbar .navbar .nav .nav-item .dropdown-item,
.header-navbar .navbar .nav .nav-item .nav-link {
  color: #ffffff;
  font-size: 14px;
  font-family: futura-pt, sans-serif;
  text-transform: uppercase;
  position: relative;
  letter-spacing: 1px;
  padding: 14px 0px; }

.header-navbar .navbar .nav .nav-item .dropdown-item {
  padding: 14px; }

.header-navbar .navbar .nav .nav-item .dropdown-menu .nav-item .nav-link {
  padding: 14px 8px; }

.header-navbar .nav .nav-item .dropdown-menu {
  background-color: #4CAAB1;
  border-radius: 0px;
  margin-top: 1em;
  border: 0px;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out; }

.header-navbar .nav .nav-item .dropdown-menu.show {
  display: block;
  -webkit-animation: fadeInFromNone 0.5s ease-out;
  animation: fadeInFromNone 0.5s ease-out; }

.header-navbar .nav .nav-item .dropdown-menu .dropdown-item:hover {
  background-color: #000000;
  color: #ffffff; }

@-webkit-keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0; }
  1% {
    display: block;
    opacity: 0; }
  100% {
    display: block;
    opacity: 1; } }

@keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0; }
  1% {
    display: block;
    opacity: 0; }
  100% {
    display: block;
    opacity: 1; } }

/****************************************************************************************/
/* nav link hover*/
/****************************************************************************************/
.header-navbar .navbar .nav .nav-item .dropdown-item:hover,
.header-navbar .navbar .nav .nav-item .nav-link:hover {
  color: #ffffff; }

.header-navbar .navbar:not(.navbar-small) .nav .nav-item .nav-link:hover,
.header-navbar .navbar:not(.navbar-small) .nav .nav-item.active .nav-link {
  color: #ffffff; }

.header-navbar .navbar .nav .nav-item > .nav-link:focus,
.header-navbar .navbar .nav .nav-item > .nav-link:active {
  color: #ffffff; }

.header-navbar .navbar.navbar-small .nav .nav-item.active > .nav-link {
  color: #ffffff; }

